"use client";

import React, { useState } from 'react';

export default function RecordsPage() {
    const [movieName, setMovieName] = useState('');
    const [movieGenre, setMovieGenre] = useState('');
    const [savedMovies, setSavedMovies] = useState([]);

    const handleAddMovie = () => {
        if (movieName && movieGenre) {
            const newMovie = { name: movieName, genre: movieGenre };
            setSavedMovies([...savedMovies, newMovie]);
            setMovieName('');
            setMovieGenre('');
        } else {
            alert('Please enter both name and genre');
        }
    };

    const handleRemoveMovie = (index) => {
        const updatedMovies = savedMovies.filter((_, i) => i !== index);
        setSavedMovies(updatedMovies);
    };

    return (
        <div>
            <h1>Records Page</h1>

            <div>
                <h2>Add a Movie/Series</h2>
                <label>
                    Name:
                    <input
                        type="text"
                        value={movieName}
                        onChange={(e) => setMovieName(e.target.value)}
                    />
                </label>
                <label>
                    Genre:
                    <input
                        type="text"
                        value={movieGenre}
                        onChange={(e) => setMovieGenre(e.target.value)}
                    />
                </label>
                <button onClick={handleAddMovie}>Add</button>
            </div>

            <div>
                <h2>Saved Movies/Series</h2>
                {savedMovies.length > 0 ? (
                    <ul>
                        {savedMovies.map((movie, index) => (
                            <li key={index}>
                                <strong>{movie.name}</strong> ({movie.genre})
                                <button
                                    onClick={() => handleRemoveMovie(index)}
                                    style={{ marginLeft: '10px' }}
                                >
                                    Remove
                                </button>
                            </li>
                        ))}
                    </ul>
                ) : (
                    <p>No movies/series saved yet.</p>
                )}
            </div>
        </div>
    );
}
